#map {
    height: 100vh;
}

body {
    margin: 0px;
    background-color: white;
    font-family: Arial, Helvetica, sans-serif;
}

.time-slider {
    right: 0.5em;
    top: .5em;
    padding: 1em !important;
}

.ol-touch .time-slider {
    top: 80px;
}

.legend {
    left: 0.5em;
    bottom: .5em;
    padding: 1em !important;
}

.ol-popup {
    position: absolute;
    background-color: white;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    padding: 15px;
    border-radius: 10px;
    border: 1px solid #cccccc;
    bottom: 12px;
    left: -50px;
    min-width: 280px;
}

.ol-popup:after,
.ol-popup:before {
    top: 100%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.ol-popup:after {
    border-top-color: white;
    border-width: 10px;
    left: 48px;
    margin-left: -10px;
}

.ol-popup:before {
    border-top-color: #cccccc;
    border-width: 11px;
    left: 48px;
    margin-left: -11px;
}

.ol-popup-closer {
    text-decoration: none;
    position: absolute;
    top: 2px;
    right: 8px;
}

.ol-popup-closer:after {
    content: "âœ–";
}